.file {
  border : 1px solid darkolivegreen;
  border-radius: 9px;
  height: 20px;
  padding : 5px 5px 5px 2px;
  margin: 80px 3px 85px 0px 
}
.tableFixHead {
  border : 1px solid grey;
  margin-bottom: 50px;
  position: relative;
  top: 30px;
  overflow-y: auto; /* make the table scrollable if height is more than 200 px  */
  width: 800px;
  max-height: 450px; /* gives an initial height of 200px to the table */
}
.tableFixHead thead th {
  position: sticky; /* make the table heads sticky */
  top: 0px; /* table head will be placed from the top of the table and sticks to it */
}
table {
  border-collapse: collapse; /* make the table borders collapse to each other */
  width: 100%;
}
th,
td {
  padding: 8px 16px;
  border: 1px solid #ccc;
}
th {
  background: #eee;
}

#search {
  border-radius: 20px;
  width: 300px; /* Set the width of the search input */
  padding: 10px; /* Add padding for spacing */
  margin-right: 3px;
}
#search::placeholder {
  opacity: 0.6; /* Change the opacity of the placeholder text */
}
#submit {
  background-color:  rgb(42, 129, 42);
  color: #fff;
  padding: 10px 20px;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s; /* Add a transition effect */
  box-shadow: 3px 3px 5px 0 rgba(0, 0, 0, 0.2);
  border-radius: 20px
}
#submit:hover {
  background-color: #1d9e12; /* Change the background color on hover */
}
#submit:active {
  background-color: #0c7936; /* Change the background color when clicked */
}